<template>
  <section class="app-main">
    <transition name="fade-transform" mode="out-in">
      <router-view :key="key" />
    </transition>
  </section>
</template>

<script>
export default {
  name: 'AppMain',
  data() {
    return {}
  },
  computed: {
    key() {
      return this.$route.fullPath
    }
  }
}
</script>

<style lang="scss">
.fixed-header + .app-main {
  padding-top: 50px;
}
// fix css style bug in open el-dialog
.el-popup-parent--hidden {
  .fixed-header {
    padding-right: 15px;
  }
}
.app-main {
  width: 100%;
  position: relative;
  overflow: hidden;
  height: 100%;
  background-color: #fff;
}
</style>
