import store from '@/store'

const { body } = document
// const WIDTH = 992 // refer to Bootstrap's responsive design
const smallScreenWidth = 760 // 小屏

export default {
  beforeMount() {
    window.addEventListener('resize', this.$_resizeHandler)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.$_resizeHandler)
  },
  mounted() {
    store.dispatch('app/toggleDevice', this.$_isMobile() ? 'mobile' : 'pc')
  },
  methods: {
    // use $_ for mixins properties
    // https://vuejs.org/v2/style-guide/index.html#Private-property-names-essential
    $_isMobile() {
      const rect = body.getBoundingClientRect()
      console.log(rect.width - 1 < smallScreenWidth)
      return rect.width - 1 < smallScreenWidth
    },
    $_resizeHandler() {
      if (!document.hidden) {
        // const isMobile = this.$_isMobile()
        const { width } = body.getBoundingClientRect()
        let isMobile = false
        if (width < smallScreenWidth) {
          isMobile = true
        }
        store.dispatch('app/toggleDevice', isMobile ? 'mobile' : 'pc')
      }
    }
  }
}
