const state = {
  nav: [], // 菜单
  banner: {}, // banner图
  companyInfo: {}, // 公司信息
  process: [], // 服务流程
  workshop: [] // 车间
}

const mutations = {
  SET_DATA: (state, data) => {
    state.nav = data.nav
    state.banner = data.banner
    state.process = data.process
    state.workshop = data.workshop
    state.companyInfo = data.companyInfo
  }
}

const actions = {
  setData({ commit }, params) {
    commit('SET_DATA', params)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
